/**
 * Back to top button styles. Implementation via scrollUp.
 * @link https://github.com/markgoodyear/scrollup/tree/v3
 * ----------------------------------------------------------------------------
 */
.back-to-top {
  display: none;
  position: fixed;
  z-index: 1000;
  bottom: 0;
  right: 1rem;
  cursor: pointer;
  margin-bottom: 1rem;
  line-height: 1;
  text-align: center;
  text-indent: 0;
  text-decoration: none;
  width: 2.625rem;
  height: 2.625rem;
  border: 3px solid rgba(0, 0, 0, 0.5);
  border-radius: 9999px;
  animation-duration: .2s;
  animation-fill-mode: both;
}

.back-to-top:before {
  font-family: Material Icons;
  content: "";
  font-size: 36px;
  width: 36px;
  height: 36px;
  line-height: 1;
  font-weight: normal;
  font-style: normal;
  font-variant: normal;
  display: inline-block;
  text-transform: none;
  letter-spacing: normal;
  speak: none;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: 'liga' 0;
}

.back-to-top:before {
  color: rgba(0, 0, 0, 0.5);
}

.back-to-top:hover:before {
  color: rgba(0, 0, 0, 0.88);
}

.back-to-top .animated {
  visibility: visible;
}

.back-to-top.animate-fade-in {
  animation-name: fade-0-to-100;
  display: block;
}

@keyframes fade-0-to-100 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.back-to-top.animate-fade-out {
  animation-name: fade-100-to-0;
  display: block;
}

@keyframes fade-100-to-0 {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

